import React, { useState, useEffect, FC } from 'react';
import { Tab } from '@ya.praktikum/react-developer-burger-ui-components';
import burgerIngredientsStyles from './burgerIngredients.module.css';
import IngredientsGroup from '../IngredientsGroup/IngredientsGroup';
import { useSelector } from '../../services/store';
import { useInView } from 'react-hook-inview';
import { TIngredient } from '../../utils/types';



const BurgerIngredients: FC = ({}) => {
    
     const {ingredients, ingredientsRequest} = useSelector(state => state.ingredients);

    //  useInView      
    const [bunsRef, inViewBuns] = useInView({ threshold: 0.35 }) as unknown as [React.RefObject<HTMLLIElement>, boolean];
    const [saucesRef, inViewSauces] = useInView({ threshold: 0.4 }) as unknown as [React.RefObject<HTMLLIElement>, boolean];;
    const [mainRef, inViewMain] = useInView({ threshold: 0.2 }) as unknown as [React.RefObject<HTMLLIElement>, boolean];;

    useEffect(() => {
        if (inViewBuns) {
            setCurrentTab("");
        } else if (inViewSauces) {
            setCurrentTab("");
        } else if (inViewMain) {
            setCurrentTab("");
        }
    }, [inViewBuns, inViewMain, inViewSauces]);


    

    //   
    const onTabClick = (tab: string) => {
        setCurrentTab(tab);
        const element = document.getElementById(tab);
        if (element) element.scrollIntoView({ behavior: "smooth" });
    };

    
    const [currentTab, setCurrentTab] = useState<string>('');
    //      
    const filterByType = (ingredientsArray: ReadonlyArray<TIngredient>, typeName: string) => {
        return ingredientsArray.filter((ingredient) => ingredient.type === typeName);
    }
    
        return(
            <section className={`mr-10 ${burgerIngredientsStyles.section}`}>
                <h1 className="mb-5 text text_type_main-large"> </h1>
                <div style={{ display: 'flex' }}>
                    <Tab value="" active={currentTab === ''} onClick={onTabClick}>
                        
                    </Tab>
                    <Tab value="" active={currentTab === ''} onClick={onTabClick}>
                        
                    </Tab>
                    <Tab value="" active={currentTab === ''} onClick={onTabClick}>
                        
                    </Tab>
                </div>
                {ingredientsRequest &&
                    <p className={`text text_type_main-large ${burgerIngredientsStyles.loader}`}>...</p>
                }
                {!ingredientsRequest &&
                <ul className={burgerIngredientsStyles.list}>
                    <IngredientsGroup scrollRef={bunsRef} titleId="" ingredients={filterByType(ingredients, "bun")}></IngredientsGroup>
                    <IngredientsGroup scrollRef={saucesRef} titleId="" ingredients={filterByType(ingredients, "sauce")}></IngredientsGroup>
                    <IngredientsGroup scrollRef={mainRef} titleId="" ingredients={filterByType(ingredients, "main")}></IngredientsGroup>
                </ul>    
                }         
            </section>
        )
}






export default BurgerIngredients;